import React from 'react';
import classnames from 'classnames';
import PT from 'prop-types';
import { Link } from 'gatsby';
import styles from './layout.module.scss';
import MenuCategories from "./MenuCategories";

class Layout extends React.PureComponent {
  render() {
    const {
      children,
      className,
      styles: htmlStyles,
    } = this.props;

    return (
      <div className={classnames(styles.root_panel, className)} styles={htmlStyles}>
        <div className={styles.grid_menu}>
          <div className={classnames(styles.menu, styles.left_menu)}>
            <div className={styles.menu_item}>
              <Link to="/" activeClassName={styles.active}>
                Blog
              </Link>
            </div>
            <div className={styles.menu_item}>
              <Link to="/calculators" activeClassName={styles.active}>
                Tools
              </Link>
            </div>
            <MenuCategories />
          </div>
          <div className={classnames(styles.menu, styles.right_menu)}>
            <div className={styles.menu_item}>
              <Link to="/cv" activeClassName={styles.active}>
                CV
              </Link>
            </div>
          </div>
        </div>
        {children}
      </div>
    );
  }
}

Layout.propTypes = {
  children: PT.node.isRequired,
  className: PT.string,
  styles: PT.objectOf(PT.oneOfType([PT.number, PT.string])),
};

Layout.defaultProps = {
  className: '',
  styles: {},
};

export default Layout;
