import React from "react";
import classnames from "classnames";
import { Link } from "gatsby";
import categories from "../../content/categories";
import styles from "./MenuCategories.module.scss";

const SubCategory = ({ link, children }) => (
  <div className={styles.item}>
    <Link to={link}>{children}</Link>
  </div>
);

class MenuDropdownCategories extends React.PureComponent {
  constructor() {
    super();
    this.state = {
      opened: false,
      hoveredOn: null,
    }
  }

  render() {
    
    return (
      <div
        onMouseEnter={() => this.setState({ opened: true })}
        onMouseLeave={() => this.setState({ opened: false })}
        className={classnames(styles.menu_item, styles.categories)}
      >
        <span
          onClick={state => this.setState({ opened: !state.opened })}
        >
          Categories
        </span>
        {
          this.state.opened && (
            <div className={styles.dropdown}>
              {categories.categories.map(cat => {
                const hasSubcategories = cat.subcategories.length > 0;
                
                return (
                  <div 
                    key={cat.name}
                    className={styles.itemWrapper}
                  >
                    <div
                      onMouseEnter={() => this.setState({ hoveredOn: cat.name })} 
                      className={styles.item}
                    >
                      <Link to={cat.link} className={styles.label}>{cat.label}</Link>
                      {hasSubcategories && <div>&gt;</div>}
                    </div>
                    <div
                      className={classnames(styles.subcategoryPanel, {[styles.open]: this.state.hoveredOn === cat.name})}
                    >
                      {
                        cat.subcategories.map((sub, i) => (
                          <SubCategory link={sub.link} key={sub.name || i}>{sub.label}</SubCategory>
                        ))
                      }
                    </div>
                  </div>
                )
              })}
            </div>
          )
        } 
      </div>
    );
  }
}

export default MenuDropdownCategories;
